body {
    /* background-color: #1f1f1f; */
    background-color: #282c35;
    font-family: sans-serif;
    margin: 0;
}
.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    color: white;
    text-align: center;
    padding: 15px 0;
}

h1, h2 {
    margin: 10px;
}
.answer-content {
    min-width: 600px;
}
.answer {
    border: 1px solid #fff;
    padding: 5px 10px 5px 45px;
    width: 100%;
    text-align: left;
    margin-top: 10px;
    position: relative;
    font-size: 22px;
    cursor: pointer;
    -webkit-transition: ease-in-out, width 0.35s ease-in-out;
    transition: ease-in-out, width 0.35s ease-in-out;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
    display: block;
    border-radius: 5px
}

.answer.selected {
    color: #fff;
    background-color: hsl(222, 13%, 25%);
    border: 1px solid #7c7c7c
}
.answer.selected:hover {
    background-color: hsl(225, 13%, 18%);
}

.answer:hover {
    background-color: #bdbcbc;
}
.answer:hover .letter {
    color: aliceblue;    
}
.answer.selected .letter {
    color: #9b9b9b;    
}


.answer:focus {
    outline: none;
}

.results span.correct {
    color: #c8ffbb;
}

.results span.failed {
    color: #f27c7c;
}

.error {
    color: #f27c7c;
}

.letter {
    color: #e13d72;
    width: 30px;
    position: absolute;
    left: 0;
    text-align: center;
    height: 28px;
    top: 0;
    padding: 5px;
    text-transform: uppercase;
}

.btn {
    color: #282c35;
    background-color: #f4f4f4;
    padding: 10px 50px;
    text-transform: uppercase;
    font-size: 18px;
    margin-top: 20px;
    cursor: pointer;
    outline: none
}

.btn-primary {
    color:#fff;
    background-color: #21272c;
    font-weight: bold;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
    border-radius: 5px

}
.btn-primary:hover {
    background-color: #262525;
}

